import React, { useState,useRef } from "react";
import "./Slider.css";
import ArrowLeftIcon from "../../node_modules/@material-ui/icons/ArrowLeft";
import ArrowRightIcon from "../../node_modules/@material-ui/icons/ArrowRight";
import { Button } from "@material-ui/core";

const Slider = () => {
    const [slideIndex, setSlideIndex] = useState(0);
    const listref=useRef();
  const handleclick = (direction) => {
    if (direction === "left") {
      setSlideIndex(slideIndex > 0 ? slideIndex - 1 : 2);
      listref.current.style.transform=`translateX(${slideIndex * -100}vw)`
      
    } else {
        setSlideIndex(slideIndex < 2 ? slideIndex + 1 : 0);
        listref.current.style.transform=`translateX(${slideIndex * -100}vw)`
    }
  };
  return (
    <div className="slider-container">
      <div
        className="arrowleft"
        direction="left"
        id="left"
        onClick={() => handleclick("left")}
      >
        <ArrowLeftIcon />
      </div>
      <div className="slider-wrapper"ref={listref}>
        <div className="slide">
          <div className="imagecontainer">
            <div className="imgslide">
              <img src="https://images.bewakoof.com/original/naam-ka-engineer-half-sleeve-t-shirt-men-s-printed-t-shirts-184765-1526721516.jpg" style={{width:"100%",height:"100%"}}/>
            </div>
          </div>
          <div className="slideinfo">
            <div className="titleslide">Engineering T -Shirts</div>
            <div className="desc">Naam Ka Engineer Half Sleeve T-Shirt</div>
            <Button>Show</Button>
          </div>
        </div>
        <div className="slide">
          <div className="imagecontainer">
            <div className="imgslide">
              <img src="https://media.kidozi.com/unsafe/600x600/img.kidozi.com/img/13822021/44606,21,2,0,12,176,176.58847736626,22,0,c27adcc4831a736d79a32b291f57451c/merchantimagenew/electrical-engineer-youth-t-shirt-black.jpg"style={{width:"100%",height:"100%"}} />
            </div>
          </div>
          <div className="slideinfo">
            <div className="titleslide">Electrical Engineer T-Shirts</div>
            <div className="desc">Electrical Engineer || Youth T-shirt</div>
            <Button>Show</Button>
          </div>
        </div>
        <div className="slide">
          <div className="imagecontainer">
            <div className="imgslide">
              <img src="https://img.joomcdn.net/8934c17e662f2496f837b316ea7c21411d51deb3_original.jpeg"style={{width:"100%",height:"100%"}} />
            </div>
          </div>
          <div className="slideinfo">
            <div className="titleslide">Yamaha Racing</div>
            <div className="desc">Yamaha Racing T Shirt Size S Xl Yz T Shirt Men Summer Casual Fashion T Shirts High Quality Short Sleeve T -Shirts</div>
            <Button>Show</Button>
          </div>
        </div>
      </div>
      <div
        className="arrowleft"
        direction="right"
        id="right"
        onClick={() => handleclick("right")}
      >
        <ArrowRightIcon />
      </div>
    </div>
  );
};

export default Slider;
