import { Badge } from "@material-ui/core";
import React from "react";
import "./Navbar.css"
import ShoppingCartIcon from '../../node_modules/@material-ui/icons/ShoppingCartOutlined';
const Navbar = () => {
  return (
    <>
      <div className="navbar-container">
        <div className="navbar-wrapper">
          <div className="navbar-left">
            <div className="language">EN</div>
            <div className="searchcontainer"><input class="navbarinput" type="search" placeholder="Search" aria-label="Search"/></div>
          </div>
          <div className="navbar-center">
            <div className="navbar-logo">T_SHIRTS</div>
          </div>
          <div className="navbar-right">
            <div className="menuitem">Register</div>
            <div className="menuitem">Sign IN</div>
            <div className="menuitem"><Badge>
              <ShoppingCartIcon/></Badge></div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Navbar;
